import { Box, Heading, Text, Image, Grid } from '@chakra-ui/react';
import styles from "../CSS/Skills.module.css";

export function Skills() {
  return (
    <Box w="100%" h="auto" id="skills" pt="50px"  className={styles.MainBox} >

      <Heading>Skills</Heading>

      <Box
        id={styles.SkillsMainContainer}
        w="auto"
        margin="auto"
        display="flex"
        mt="20px"
        h="auto"
        // border="1px solid black"
        justifyContent="space-around"
       >

        <Box boxShadow="rgb(252, 199, 100) 3.95px 3.95px 2.6px" h="520px" borderRadius="10px"   className={styles.cards} >

          <Text fontSize="3xl">Front End</Text>

          <Box display="grid" gridTemplateColumns='repeat(2, 1fr)' gap={6} id="grid" p="20px" >
            <Box align="center" >
              <Image
                
                src="https://img.icons8.com/fluency/512/html-5.png"
                alt="html"
                w="100px"
              />
              <Text>HTML</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/color/512/css3.png"
                alt="html"
                w="100px"
              />
              <Text>CSS</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/arcade/512/js.png"
                alt="html"
                w="100px"
              />
              <Text>JAVASCRIPT</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/external-justicon-flat-justicon/512/external-atom-science-justicon-flat-justicon.png"
                alt="html"
                w="100px"
              />
              <Text>REACT</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/color/512/chakra-ui.png"
                alt="html"
                w="100px"
              />
              <Text>CHAKRA-UI</Text>
            </Box>

          </Box>
        </Box>

        <Box boxShadow="rgb(252, 199, 100) 3.95px 3.95px 2.6px" h="520px" borderRadius="10px" className={styles.cards} >

          <Text fontSize="3xl">Back End</Text>

          <Box display="grid" gridTemplateColumns='repeat(2, 1fr)' gap={6} id="grid" p="20px" >
            <Box align="center">
              <Image
                src="https://img.icons8.com/color/512/redux.png"
                alt="html"
                w="100px"
              />
              <Text>REDUX</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/fluency/512/node-js.png"
                alt="html"
                w="100px"
              />
              <Text>NODE-JS</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/nolan/512/express-js.png"
                alt="html"
                w="100px"
              />
              <Text>EXPRESS-JS</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/external-tal-revivo-shadow-tal-revivo/512/external-mongodb-a-cross-platform-document-oriented-database-program-logo-shadow-tal-revivo.png"
                alt="html"
                w="100px"
              />
              <Text>MONGO-DB</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://opencollective-production.s3-us-west-1.amazonaws.com/efa23630-0509-11e8-bf7d-fb9c62253aad.png"
                alt="html"
                w="100px"
                // border="1px solid black"
              />
              <Text >CYPRESS</Text>
            </Box>

          </Box>
        </Box>

        <Box boxShadow="rgb(252, 199, 100) 3.95px 3.95px 2.6px" h="520px" borderRadius="10px" className={styles.cards} >

          <Text fontSize="3xl">Others</Text>

          <Box display="grid" gridTemplateColumns='repeat(2, 1fr)' gap={6} id="grid" p="20px" >
            <Box align="center">
              <Image
                src="https://img.icons8.com/plasticine/512/github-squared.png"
                alt="html"
                w="100px"
              />
              <Text>GITHUB</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/color/2x/git.png"
                alt="html"
                w="100px"
              />
              <Text>GIT</Text>
            </Box>

            <Box align="center" >
            <Image
                src="https://img.icons8.com/color/512/visual-studio.png"
                alt="html"
                w="100px"
              />
              <Text>VISUAL CODE</Text>
            </Box>

           

            <Box align="center" >
            <Image
                src="https://img.icons8.com/color/512/npm.png"
                alt="html"
                w="100px"
              />
              <Text>NPM</Text>
            </Box>

          </Box>
        </Box>
      </Box>
    </Box>
  )
}
