import { Box, Heading, Image, Text, Link } from '@chakra-ui/react'
import styles from '../CSS/Home.module.css'

export function Home() {
  return (
    <Box id="home" w="100%"
    className={styles.homeMainBox}
    h="auto"
    display="flex"
    justifyContent="space-evenly"
    alignItems="center"
    // border="1px solid black"
    
    >
      <Box
        w="100%"
        h="500px"
        display="flex"
        justifyContent="space-evenly"
        alignItems="center"
        id={styles.box}
        // border="1px solid black"
      >
        <img src="/prateek.jpg" alt="prateek.jpg" id={styles.image} />

        <Box borderRadius="50px" w="auto" p="10px" boxShadow="2xl" id={styles.greeting}>
          <Heading size="3xl" align="left" w="auto" id={styles.heading}>
            Greetings, I am Prateek !!
          </Heading>
          <Text fontSize="4xl" align="left" w="auto" mt=" 20px" id={styles.text1}>
            Full Stack Web Developer
          </Text>
          <Text fontSize="4xl" align="left" mt="20px" w="auto" id={styles.text2}>
            From Lucknow,Uttar Pradesh{' '}
          </Text>

          <Box
            display=" flex"
            w="auto"
            ml="20px"
            mt="20px"
            id={styles.contact}
          >
            <Link href="https://www.linkedin.com/in/prateek-bhattacharya-35118123b/" isExternal >
                <Image
                  src="https://img.icons8.com/color/512/linkedin.png"
                  alt="linkedIn"
                  w="100px"
                  id={styles.img1}
                />
              </Link>

              <Link href="https://github.com/prateekoctane" isExternal >
                <Image
                  src="https://img.icons8.com/plasticine/512/github-squared.png"
                  alt="github"
                  w="100px"
                  id={styles.img2}
                />
              </Link>
          </Box>
        </Box>
      </Box>
      </Box>
  )
}
